body {
    display: flex;
    height: 100%;
    margin: 0;
    padding: 0;
    background-image: url("../src/images/back.jpg");
    background-size:cover;
    background-repeat: no-repeat;
    justify-content: center;
}

@font-face {
    font-family: 'BlenderPro-Medium';
    src: url('fonts/BlenderPro-Medium.eot'), url('fonts/BlenderPro-Medium.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

.hidden{
    display: none;
}

.game {
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.modal-container{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modalw{
    background-color: white;
    width: 500px;
    height: 450px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: #9C19E0 2px solid;
    border-radius: 10px;
    background-color: hsla(233, 100%, 21%, 0.504);
    font-family: 'BlenderPro-Medium';
    font-style: normal;
    font-weight: 400;
    color: #F3E600;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                        supported by Chrome, Edge, Opera and Firefox */
}

.modal-head{
    font-size: 44px;
    padding: 14px;
}

.modal-link{
    font-size: 20px;
    text-align: center;
}

.copied{
    text-align: center;
    background-color: black;
}

.modal-link:hover {
    text-decoration: underline;
    cursor: pointer;
}

.modal-input{
    width: 240px;
    height: 50px;
    margin: auto;
    margin-left: 70px;
    margin-right: 5px;
    border: #F3E600 3px solid;
    font-size: 18px;
}

.modal-button-block{
    margin-top: 30px;
    display: flex;
    flex-direction: row;
}

.modal-button{
    margin: auto;
    margin-left: 5px;
    height: 76px;
    width: 100px;
    font-size: 16px;
    border-radius: 14px;
    background-color: 	#8ea5ff;
    color: #F3E600;
}

.modal-text{
    margin-top: 30px;
    text-align: center;
    font-size: 18px;
}

.modal-text-win{
    margin-top: 30px;
    text-align: center;
    margin-top: 180px;
    font-size: 50px;
}

.gui{
    height: 100vh;
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.player-img{
    margin: auto;
    width: 140px;
    height: 140px;
    border-radius: 10px;
    border: 3px solid #02d7f2;
}

.player-tab{
    width: 240px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.player-name{
    margin: 12px 0 5px 0;
    font-family: 'BlenderPro-Medium';
    font-style: normal;
    font-weight: 400;
    color: #F3E600;
    font-size: 26px;
    line-height: 22px;
    text-align: center;
}

/* .name-white{
    border: 1px solid #ccffff;
}

.name-black{
    border: 1px solid black;
} */

.bones{
    margin-top: 95px;
    margin-bottom: 95px;
    padding: 5px;
    display: flex;
    flex-direction: row;
    justify-content: center
}

.bone{
    height: 50px;
    width: 50px;
    margin-left: 5px;
    margin-right: 5px;
    border: black 1px solid;
    background: radial-gradient(circle, #9C19E0 55%, 	#000D6B 100%);
    border-radius: 6px 6px 6px 6px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.num{
    margin: 0;
    padding: 0;
    font-family: 'BlenderPro-Medium';
    font-style: normal;
    font-weight: 400;
    color: #F3E600;
    font-size: 36px;
    line-height: 22px;
    text-align: center;
}

.dev-instr{
    display: flex;
    flex-direction: column;
}

.info-tab{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 220px;
    margin-left: 40px;
    margin-right: auto;
    height: 400px;
    border: #9C19E0 2px solid;
    border-radius: 10px;
    background-color: hsla(233, 100%, 21%, 0.504);
}

.turn-info{
    height: 200px;
}

.turn-text{
    margin: 15px;
    font-family: 'BlenderPro-Medium';
    font-style: normal;
    font-weight: 400;
    color: #F3E600;
    font-size: 36px;
    line-height: 22px;
    text-align: center;
}

.chips-info-text{
    margin: 15px;
    font-family: 'BlenderPro-Medium';
    font-style: normal;
    font-weight: 400;
    color: #F3E600;
    font-size: 28px;
    line-height: 22px;
    text-align: center;
}